<template>
  <div class="pageContent">
    <!-- 时间轴 -->
    <div class="timeBox">
      <!-- 待审核 -->
      <div v-if="form.auditStatus == 10">
        <el-steps :active="1" finish-status="success" align-center>
          <el-step title="提交申请，待审核"></el-step>
          <el-step title="审核中"></el-step>
        </el-steps>
      </div>
      <!-- 已驳回 -->
      <div v-if="form.auditStatus == 20">
        <el-steps :active="1" finish-status="success" align-center>
          <el-step title="提交申请"></el-step>
          <el-step title="卖家驳回"></el-step>
        </el-steps>
      </div>
      <!-- 待交货 -->
      <div v-if="form.auditStatus == 24">
        <el-steps :active="2" finish-status="success" align-center>
          <el-step title="提交申请，待审核"></el-step>
          <el-step title="卖家同意退款，待交货"></el-step>
          <el-step title="已交货"></el-step>
          <el-step title="已收货"></el-step>
          <el-step title="已退款"></el-step>
        </el-steps>
      </div>
      <!-- 已交货 -->
      <div v-if="form.auditStatus == 30">
        <el-steps :active="3" finish-status="success" align-center>
          <el-step title="提交申请，待审核"></el-step>
          <el-step title="卖家同意退款，待交货"></el-step>
          <el-step title="已交货"></el-step>
          <el-step title="已收货"></el-step>
          <el-step title="已退款"></el-step>
        </el-steps>
      </div>
      <!-- 已驳回 -->
      <div v-if="form.auditStatus == 32">
        <el-steps :active="4" finish-status="success" align-center>
          <el-step title="提交申请，待审核"></el-step>
          <el-step title="卖家同意退款，待交货"></el-step>
          <el-step title="已交货"></el-step>
          <el-step title="已收货"></el-step>
          <el-step title="已退款"></el-step>
        </el-steps>
      </div>
      <!-- 退款中 -->
      <div v-if="form.auditStatus == 40">
        <el-steps :active="5" finish-status="success" align-center>
          <el-step title="提交申请，待审核"></el-step>
          <el-step title="卖家已同意，待交货"></el-step>
          <el-step title="已交货"></el-step>
          <el-step title="已收货"></el-step>
          <el-step title="退款中"></el-step>
        </el-steps>
      </div>
      <!-- 已退款 -->
      <div v-if="form.auditStatus == 50">
        <el-steps :active="5" finish-status="success" align-center>
          <el-step title="提交申请，待审核"></el-step>
          <el-step title="卖家同意退款，待交货"></el-step>
          <el-step title="已交货"></el-step>
          <el-step title="已收货"></el-step>
          <el-step title="已退款"></el-step>
        </el-steps>
      </div>
      <!-- 已撤销 -->
      <div v-if="form.auditStatus == 60">
        <el-steps :active="2" finish-status="success" align-center>
          <el-step title="提交申请"></el-step>
          <el-step title="撤销退款申请"></el-step>
        </el-steps>
      </div>
      <!-- 已拒收 -->
      <div v-if="form.auditStatus == 80">
        <el-steps :active="4" finish-status="success" align-center>
          <el-step title="提交申请，待审核"></el-step>
          <el-step title="卖家同意退款，待交货"></el-step>
          <el-step title="已交货"></el-step>
          <el-step title="已拒收，服务单关闭"></el-step>
        </el-steps>
      </div>
    </div>
    <!-- 订单信息 -->
    <el-card shadow="always" style="margin-bottom:20px;">
      <div class="pageHead">退单信息</div>
      <!-- 待审核 - 表头 -->
      <div v-if="form.auditStatus == 10">
        <el-card class="orderDetail waitpay" shadow="always">
          <div class="flex-row-between" style="margin-bottom:20px;">
            <div class="detailTitle flex-row-start">
              <el-image class="detailImg" :src="require('../../../assets/images/afterSale/dsh.png')" fit="fill"></el-image>
              <div>待审核</div>
            </div>
            <div class="detailLi">退款单编号：{{form.afterSaleNo}}</div>
          </div>
          <div class="detailLi">申请退款时间：{{form.createTime}}</div>
        </el-card>
      </div>
      <!-- 已驳回 - 表头 -->
      <div v-if="form.auditStatus == 20">
        <el-card class="orderDetail hadsend" shadow="always">
          <div class="flex-row-between" style="margin-bottom:20px;">
            <div class="detailTitle flex-row-start">
              <el-image class="detailImg" :src="require('../../../assets/images/afterSale/ybh.png')" fit="fill"></el-image>
              <div>已驳回</div>
            </div>
            <div class="detailLi">退款单编号：{{form.afterSaleNo}}</div>
          </div>
          <div class="detailLi">申请退款时间：{{form.createTime}}</div>
          <div class="detailLi">驳回时间：{{form.rejectTime}}</div>
        </el-card>
      </div>
      <!-- 待交货 - 表头 -->
      <div v-if="form.auditStatus == 24">
        <el-card class="orderDetail waitpay" shadow="always">
          <div class="flex-row-between" style="margin-bottom:20px;">
            <div class="detailTitle flex-row-start">
              <el-image class="detailImg" :src="require('../../../assets/images/afterSale/djh.png')" fit="fill"></el-image>
              <div>待交货</div>
            </div>
            <div class="detailLi">退款单编号：{{form.afterSaleNo}}</div>
          </div>
          <div class="detailLi">申请退款时间：{{form.createTime}}</div>
          <div class="detailLi">同意退货时间：{{form.approveTime}}</div>
        </el-card>
      </div>
      <!-- 已交货 - 表头 -->
      <div v-if="form.auditStatus == 30">
        <el-card class="orderDetail hadsend" shadow="always">
          <div class="flex-row-between" style="margin-bottom:20px;">
            <div class="detailTitle flex-row-start">
              <el-image class="detailImg" :src="require('../../../assets/images/afterSale/yjh.png')" fit="fill"></el-image>
              <div>已交货</div>
            </div>
            <div class="detailLi">退款单编号：{{form.afterSaleNo}}</div>
          </div>
          <div class="detailLi">申请退款时间：{{form.createTime}}</div>
          <div class="detailLi">同意退货时间：{{form.approveTime}}</div>
          <div class="detailLi">交货时间：{{form.deliverTime}}</div>
        </el-card>
      </div>
      <!-- 已收货 - 表头 -->
      <div v-if="form.auditStatus == 32">
        <el-card class="orderDetail hadsend" shadow="always">
          <div class="flex-row-between" style="margin-bottom:20px;">
            <div class="detailTitle flex-row-start">
              <el-image class="detailImg" :src="require('../../../assets/images/afterSale/ysh.png')" fit="fill"></el-image>
              <div>已收货</div>
            </div>
            <div class="detailLi">退款单编号：{{form.afterSaleNo}}</div>
          </div>
          <div class="detailLi">申请退款时间：{{form.createTime}}</div>
          <div class="detailLi">同意退货时间：{{form.approveTime}}</div>
          <div class="detailLi">交货时间：{{form.deliverTime}}</div>
          <div class="detailLi">确认收货时间：{{form.confimTime}}</div>
        </el-card>
      </div>
      <!-- 退款中 - 表头 -->
      <div v-if="form.auditStatus == 40">
        <el-card class="orderDetail waitpay" shadow="always">
          <div class="flex-row-between" style="margin-bottom:20px;">
            <div class="detailTitle flex-row-start">
              <el-image class="detailImg" :src="require('../../../assets/images/afterSale/tkz.png')" fit="fill"></el-image>
              <div>退款中</div>
            </div>
            <div class="detailLi">退款单编号：{{form.afterSaleNo}}</div>
          </div>
          <div class="detailLi">申请退款时间：{{form.createTime}}</div>
          <div class="detailLi">同意退货时间：{{form.approveTime}}</div>
          <div class="detailLi">交货时间：{{form.deliverTime}}</div>
          <div class="detailLi">确认收货时间：{{form.confimTime}}</div>
          <div class="detailLi">发起退款时间：{{form.refundTime}}</div>
        </el-card>
      </div>
      <!-- 已退款 - 表头 -->
      <div v-if="form.auditStatus == 50">
        <el-card class="orderDetail hadsend" shadow="always">
          <div class="flex-row-between" style="margin-bottom:20px;">
            <div class="detailTitle flex-row-start">
              <el-image class="detailImg" :src="require('../../../assets/images/afterSale/ytk.png')" fit="fill"></el-image>
              <div>已退款</div>
            </div>
            <div class="detailLi">退款单编号：{{form.afterSaleNo}}</div>
          </div>
          <div class="detailLi">申请退款时间：{{form.createTime}}</div>
          <div class="detailLi">同意退货时间：{{form.approveTime}}</div>
          <div class="detailLi">交货时间：{{form.deliverTime}}</div>
          <div class="detailLi">确认收货时间：{{form.confimTime}}</div>
          <div class="detailLi">退款发起时间：{{form.refundTime}}</div>
          <div class="detailLi">退款到账时间：{{form.updataTime}}</div>
        </el-card>
      </div>
      <!-- 已撤销 - 表头 -->
      <div v-if="form.auditStatus == 60">
        <el-card class="orderDetail hadsend" shadow="always">
          <div class="flex-row-between" style="margin-bottom:20px;">
            <div class="detailTitle flex-row-start">
              <el-image class="detailImg" :src="require('../../../assets/images/afterSale/ycx.png')" fit="fill"></el-image>
              <div>已撤销</div>
            </div>
            <div class="detailLi">退款单编号：{{form.afterSaleNo}}</div>
          </div>
          <div class="detailLi">申请退款时间：{{form.createTime}}</div>
          <div class="detailLi">撤销退款时间：{{form.cancelTime}}</div>
        </el-card>
      </div>
      <!-- 退款失败 - 表头 -->
      <div v-if="form.auditStatus == 70">
        <el-card class="orderDetail fail" shadow="always">
          <div class="flex-row-between" style="margin-bottom:20px;">
            <div class="detailTitle flex-row-start">
              <el-image class="detailImg" :src="require('../../../assets/images/afterSale/tksb.png')" fit="fill"></el-image>
              <div>退款失败</div>
            </div>
            <div class="detailLi">退款单编号：{{form.afterSaleNo}}</div>
          </div>
          <div class="detailLi">申请退款时间：{{form.createTime}}</div>
          <div class="detailLi">发起退款时间：{{form.refundTime}}</div>
          <div class="detailLi">失败原因：{{form.refundFailReason}}</div>
        </el-card>
      </div>
      <!-- 退款失败 - 表头 -->
      <div v-if="form.auditStatus == 80">
        <el-card class="orderDetail fail" shadow="always">
          <div class="flex-row-between" style="margin-bottom:20px;">
            <div class="detailTitle flex-row-start">
              <el-image class="detailImg" :src="require('../../../assets/images/afterSale/tksb.png')" fit="fill"></el-image>
              <div>已拒收</div>
            </div>
            <div class="detailLi">退款单编号：{{form.afterSaleNo}}</div>
          </div>
          <div class="detailLi">申请退款时间：{{form.createTime}}</div>
          <div class="detailLi">同意退货时间：{{form.approveTime}}</div>
          <div class="detailLi">交货时间：{{form.deliverTime}}</div>
          <div class="detailLi">拒收时间：{{form.rejectionTime}}</div>
        </el-card>
      </div>
    </el-card>
    <!-- 退货商品图片（买家上传） -->
    <el-card shadow="always" v-if="questionImage.length > 0" style="margin-bottom:20px;">
      <div class="pageHead">退货商品图片（买家上传）</div>
      <div class="flex-row-start">
        <el-image 
          v-for="(item,index) in questionImage"
          :key="index"
          style="width: 100px; height: 100px; margin-right:20px;"
          :src="item" 
          :preview-src-list="[item]">
        </el-image>
      </div>
    </el-card>
    <!-- 商品信息 -->
    <el-card v-if="form.auditStatus != 20 && form.auditStatus != 60" shadow="always" style="margin-bottom:20px;">
      <div class="pageHead">退货商品信息</div>
      <el-table :data="[form]" border style="width: 100%">
        <el-table-column label="商品信息" width="500">
          <template slot-scope="scope">
            <div class="flex-row-start">
              <img style="width: 100px; height: 100px" :src="scope.row.imageUrl" alt="">
              <!-- <el-image style="width: 100px; height: 100px" :src="scope.row.imageUrl" fit="fill"></el-image> -->
              <div style="flex:1;margin-left:10px;text-align:left;">{{scope.row.productName}}</div>
            </div>
          </template>
        </el-table-column>
        <el-table-column label="商品编号">
          <template slot-scope="scope">
            <div>{{scope.row.productCode ? scope.row.productCode : '-'}}</div>
          </template>
        </el-table-column>
        <el-table-column prop="salePrice" label="退货单价"></el-table-column>
        <el-table-column prop="productCount" label="退货数量"></el-table-column>
        <el-table-column label="优惠金额">
          <template slot-scope="scope">
            <div>{{scope.row.couponDiscountPrice ? scope.row.couponDiscountPrice + '优惠券优惠金额' : ''}}</div>
            <div>{{scope.row.selfDiscountPrice ? scope.row.selfDiscountPrice + '自定义优惠金额' : ''}}</div>
            <div>{{scope.row.promotionDiscountPrice ? scope.row.promotionDiscountPrice + '促销优惠金额，满减，满折' : ''}}</div>
            <div v-if="scope.row.activityDiscountPrice">{{scope.row.activityDiscountPrice}} {{scope.row.activityType == 1 ? '限时折扣' : ''}} {{scope.row.activityType == 2 ? '砍价' : ''}}</div>
            <div>{{scope.row.vipDiscountPrice ? scope.row.vipDiscountPrice + '会员优惠金额' : ''}}</div>
            <div v-if="
              !scope.row.couponDiscountPrice &&
              !scope.row.selfDiscountPrice && 
              !scope.row.promotionDiscountPrice && 
              !scope.row.activityDiscountPrice && 
              !scope.row.vipDiscountPrice
            "
            > - </div>
          </template>
        </el-table-column>
        <el-table-column prop="payPrice" label="应退货金额"></el-table-column>
      </el-table>
      <div style="float: left;margin: 5px;width: 100%;text-align: right;padding-right: 30px;">
        总退货金额：
        <span class="color-danger">￥{{form.totalRefundPrice}}</span>
      </div>
      <div v-if="form.deliveryType == 0" style="float: left;margin: 5px;width: 100%;text-align: right;padding-right: 30px;">
        运费：
        <span class="color-danger">￥{{form.freightPrice}}</span>
      </div>
      <div style="float: left;margin: 5px;width: 100%;text-align: right;padding-right: 30px;">
        <span style="border-top:1px solid #303133;padding:5px 0 0 50px">
          <span style="font-weight:700">合计：</span>   <span style="font-size:17px">总{{form.auditStatus == 50 ? '实' : '应'}}退金额：</span>
          <span class="color-danger">￥{{form.totalPayPrice}}</span>
        </span>
      </div>
    </el-card>
    <!-- 退款单信息 -->
    <el-card shadow="always" style="margin-bottom:20px;">
      <div class="pageHead">退款单信息</div>
      <ul class="tkInfo flex-row-start fw">
        <li class="tkLi">订单号：{{form.orderNo}}</li>
        <!-- 当前状态 -->
        <li class="tkLi" v-if="form.currOrderStatus == 0">订单当前状态：待付款</li>
        <li class="tkLi" v-if="form.currOrderStatus == 1">订单当前状态：待发货</li>
        <li class="tkLi" v-if="form.currOrderStatus == 2">订单当前状态：已发货</li>
        <li class="tkLi" v-if="form.currOrderStatus == 3">订单当前状态：已完成</li>
        <li class="tkLi" v-if="form.currOrderStatus == 4">订单当前状态：已关闭</li>
        <!-- 取消时状态 -->
        <li class="tkLi" v-if="form.orderStatus == 0">订单取消时状态：待付款</li>
        <li class="tkLi" v-if="form.orderStatus == 1">订单取消时状态：待发货</li>
        <li class="tkLi" v-if="form.orderStatus == 2">订单取消时状态：已发货</li>
        <li class="tkLi" v-if="form.orderStatus == 3">订单取消时状态：已完成</li>
        <li class="tkLi" v-if="form.orderStatus == 4">订单取消时状态：已关闭</li>
        <!-- 退款原因 -->
        <li class="tkLi">退款原因：{{form.baseName ? form.baseName : '-'}}</li>
        <!-- 退单类型 -->
        <li class="tkLi" v-if="form.type == 0">退单类型：仅退款</li>
        <li class="tkLi" v-if="form.type == 10">退单类型：退货退款</li>
        <li class="tkLi" v-if="form.type == 20">退单类型：换货</li>
        <li class="tkLi" v-if="form.type == 30">退单类型：维修</li>
        <!-- 退款金额 -->
        <li class="tkLi">申请退款金额：￥{{form.applyAmount}}</li>
        <!-- 是否扣除运费 -->
        <li class="tkLi" v-if="form.auditStatus == 40 || form.auditStatus == 50">是否扣除运费：{{form.isFreight == 0 ? '否' : '是'}}</li>
        <!-- 实际退款金额 -->
        <li class="tkLi" v-if="form.auditStatus == 40 || form.auditStatus == 50">实际退款金额：￥{{form.refundAmount}}</li>
        <!-- 退款说明 -->
        <li class="tkLi" v-if="form.auditStatus == 40 || form.auditStatus == 50">退款说明：{{form.refundDesc ? form.refundDesc : '-'}}</li>
        <!-- 退款备注 -->
        <li class="tkLi">退款备注：{{form.questionDesc ? form.questionDesc : '-'}}</li>
        <!-- 商家驳回原因 -->
        <li class="tkLi" v-if="form.auditStatus == 20">商家驳回原因：{{form.reason ? form.reason : '-'}}</li>
        <!-- 拒收原因： -->
        <li class="tkLi" v-if="form.auditStatus == 80">拒收原因：{{form.refundReason}}</li>
      </ul>
    </el-card>
    <!-- 返还信息-->
    <el-card v-if="form.auditStatus == 30 || form.auditStatus == 32 || form.auditStatus == 40 || form.auditStatus == 50 || form.auditStatus == 70" shadow="always" style="margin-bottom:20px;">
      <div class="pageHead">返还信息</div>
      <el-form :inline="true" :model="form" class="demo-form-inline" label-width="140px">
        <el-form-item label="返还方式：">
          {{form.deliveryType == 0 ? '物流' : '自提'}}
        </el-form-item>
        <el-form-item v-if="form.deliveryType == 0" label="物流公司：">
          {{form.expressName}}
        </el-form-item>
        <el-form-item v-if="form.deliveryType == 0" label="快递单号：">
          {{form.expressCode}}
        </el-form-item>
      </el-form>
    </el-card>
    <div class="flex-row-start">
      <el-button v-if="form.auditStatus == 10" type="primary" @click="rejectItem">驳回</el-button>
      <el-button v-if="form.auditStatus == 10" type="primary" @click="agreeItem">通过</el-button>
      <el-button v-if="form.auditStatus == 32" type="primary" @click="sureItem">同意退款</el-button>
      <el-button :loading="againLoading" v-if="form.auditStatus == 70" type="primary" @click="againItem">再次退款</el-button>
      <el-button :loading="getLoading" v-if="form.auditStatus == 30" type="primary" @click="getItem">确认收货</el-button>
      <el-button :loading="againLoading" v-if="form.auditStatus == 30" type="primary" @click="noItem">拒收</el-button>
      <el-button :loading="againLoading" v-if="form.auditStatus != 10 && form.auditStatus != 24 && form.auditStatus != 20 && form.auditStatus != 80 && form.auditStatus != 60" @click="showpost" type="primary">追踪物流</el-button>
      <el-button @click="goBack">返回上一页</el-button>
    </div>
    <!-- 驳回弹出框 -->
    <rejectShow :rejectShow="rejectShow" :rejectForm="rejectForm" @rejectShowClose="rejectShowClose" @rejectFun="rejectFun"></rejectShow>
    <!-- 追踪物流 -->
    <postTree :postShow="postShow" :postNum="postNum" @closeTree="closeTree"></postTree>
    <!-- 拒收弹出框 -->
    <rejectionShow :rejectionShow="rejectionShow" :rejectionForm="rejectionForm" :rejectionLoading="rejectionLoading" @rejectionShowClose="rejectionShowClose" @rejectionFun="rejectionFun"></rejectionShow>
    <!-- 退款弹出框 -->
    <makeSureShow :makeSureShow="makeSureShow" :makeSureId="makeSureId" :makeSureLoading="makeSureLoading" @makeSureClose="makeSureClose" @makeSureFun="makeSureFun"></makeSureShow>
    <!-- 同意退货弹出框 -->
    <showAddress :addressShow="addressShow" @addressShowClose="addressShowClose" @addressFun="addressFun"></showAddress>
    
  </div>
</template>
<script>
import { mapState } from 'vuex'
import { continueRefund} from '@/api/afterSaleManage/refund'
import { 
  findById ,
  reject ,
  approve,
  rejectReason,
  approveAmount
} from '@/api/afterSaleManage/returnRefund'
import rejectShow from '../components/rejectShow';
import rejectionShow from '../components/rejectionShow';
import makeSureShow from '../components/makeSureShow';
import postTree from '@/components/postTree/postTree';
import showAddress from '../components/showAddress';

export default {
  data(){
    return{
      form:{},                  // 详情数据
      rejectShow:false,
      rejectLoading:false,
      rejectForm:{},
      againLoading:false,             // 再次退款loading
      getLoading:false,               //
      postShow:false,
      postNum:'',
      rejectionShow:false,
      rejectionForm:{},
      rejectionLoading:false,
      addressShow:false,
      addressLoading:true,
      agreeForm:{},
      // 确认退款弹出框数据  
      makeSureShow:false,
      makeSureId:'',
      makeSureLoading:false,      
      questionImage:[],               // 买家上传的图片

    }
  },
  created(){
    this.getData()
  },
  methods:{
    getData(){
      findById({id:this.$route.query.id}).then(res=>{
        console.log('售后订单详情',res)
        if(res.code == 200){
          this.form = res.data
          if(this.form.questionImage) this.questionImage = this.form.questionImage.split(",")
        }else{
          this.$message.error(res.message);
        }
      })
    },
    makeSureFun(e){
      console.log('父',e)
      this.makeSureLoading = true;
      approveAmount(e).then(res=>{
        console.log('同意退款',res)
        if(res.code == 200){
          this.$message({
            message: res.message,
            type: 'success'
          });
          this.makeSureShow = false;
          setTimeout(()=>{
            this.makeSureLoading = false;
            this.$router.replace({
              path:'/afterSaleManage/returnRefund'
            })
          },1000)
        }else{
          this.$message.error(res.message);
          this.makeSureLoading = false;
        }
      })
    },
    makeSureClose(){
      this.makeSureShow = false;
    },
    // 
    sureItem(){
      this.makeSureId = this.form.id
      this.makeSureShow = true;
    },
    // 已交货 - 拒绝退款
    noItem(){
      this.rejectionShow = true;
      this.rejectionForm.id = this.form.id;
      this.rejectionForm.state = 80;
    },
    rejectionShowClose(e){
      this.rejectionShow = false;
    },
    rejectionFun(e){
      console.log('父',e)
      this.rejectionLoading = true;
      rejectReason(e).then(res=>{
        console.log('拒收',res)
        if(res.code == 200){
          this.$message({
            message: res.message,
            type: 'success'
          });
          this.rejectionShow = false
          setTimeout(()=>{
            this.$router.replace({
              path:'/afterSaleManage/returnRefund'
            })
            this.rejectionLoading = false;
          },1000)
        }else{
          this.rejectionLoading = false;
          this.$message.error(res.message);
        }
      })
    },
    // 已交货 - 确认收货
    getItem(){
      this.$confirm('您确认已经收到卖家的退货吗?', '收货确认', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        let obj = {
          id:this.form.id,
          state:32
        }
        approve(obj).then(res=>{
          console.log('确认收货',res)
          if(res.code == 200){
            this.$message({
              type: 'success',
              message: res.message
            });
            setTimeout(()=>{
              this.getLoading = false;
              this.$router.replace({
                path:'/afterSaleManage/returnRefund'
              })
            },1000)
          }else{
            this.getLoading = false;
            this.$message.error(res.message);
          }
        })
      }).catch(() => {});
    },
    // 退款失败 - 再次退款
    againItem(e){
      this.$confirm(`退款金额：￥${this.form.applyAmount}`, '再次退款确认', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.againLoading = true
        continueRefund({id:this.form.id}).then(res=>{
          console.log('重新退款',res)
          if(res.code == 200){
            this.$message({
              message: res.message,
              type: 'success'
            });
            setTimeout(()=>{
              this.againLoading = false;
              this.$router.replace({
                path:'/afterSaleManage/returnRefund'
              })
            },1000)
          }else{
            this.againLoading = false;
            this.$message.error(res.message);
          }
        })
      }).catch(() => {});
    },
    showpost(){
      this.postShow = true;
      // this.postNum = 4300775311159
      this.postNum = this.form.expressCode
    },
    closeTree(){
      this.postShow = false;
    },
    // 返回上一页
    goBack(){
      this.$router.go(-1)
    },
    // 列表 - 驳回
    rejectItem(){
      this.rejectForm.id = this.form.id
      this.rejectShow = true
    },
    // 驳回 - 关闭
    rejectShowClose(e){
      console.log('父级关闭',e)
      this.rejectShow = false
    },
    // 驳回 - 请求
    rejectFun(e){
      console.log('父级确认',e)
      this.rejectLoading = true;
      reject(e).then(res => {
        console.log('驳回',res)
        if(res.code == 200){
          this.$message({
            message: res.message,
            type: 'success'
          });
          this.rejectShow = false;
          setTimeout(()=>{
            this.$router.replace({
              path:'/afterSaleManage/returnRefund'
            })
            this.rejectLoading = false;
          },1000)
        }else{
          this.rejectLoading = false;
          this.$message.error(res.message);
        }
      })
    },
    // 同意退货退款
    agreeItem(){
      this.addressShow = true;
      console.log(this.form)
      this.agreeForm.id = this.form.id
      this.agreeForm.state = 24
    },
    addressShowClose(){
      this.addressShow = false;
      this.agreeForm.id = ''
      this.agreeForm.state = ''
    },
    addressFun(){
      console.log('执行了')
      this.addressLoading = true;
      approve(this.agreeForm).then(res=>{
        console.log('同意退货退款',res)
        if(res.code == 200){
          this.$message({
            message: res.message,
            type: 'success'
          });
          this.addressShow = false;
          this.getData()
          setTimeout(()=>{
            this.addressLoading = false;
          },1000)
        }else{
          this.addressLoading = false;
          this.$message.error(res.message);
        }
      })
    },
  },
  components:{
    rejectShow,
    postTree,
    rejectionShow,
    makeSureShow,
    showAddress
  },
  computed: {
    ...mapState([
      'user',
      'shop'
    ])
  },
}
</script>
