    &.hadsend{
      background-color: rgba(63, 203, 31, 0.3);
      .detailTitle{
        color: #3FCB1F;
      }
    }
    &.fail{
      background-color: rgba(231, 43, 43, 0.3);
      .detailTitle{
        color: #E72B2B;
      }
    }
    .detailTitle{
      line-height: 40px;
      font-weight: bold;
      font-size: 18px;
      .detailImg{
        width: 26px;
        height: 26px;
        margin-right: 10px;
      }
    }
    .detailLi{
      color: #333333;
      line-height: 40px;
    }
    .fontLi{
      width: 100%;
      line-height: 24px;
      font-size: 14px;
      color: #333333;
    }
  }
  .remarkLi{
    font-size: 14px;
    line-height: 20px;
    margin-bottom: 20px;
    .remarkT{
      color: #909399;
    }
    .remarkV{
      margin-left: 20px;
      color: #303133;
    }
  }
  .tkInfo{
    padding: 0 40px;
    .tkLi{
      width: 33%;
      margin-bottom: 20px;
      color: #606266;
    }
  }
</style>